.footer {
  text-align: center;
  background: #323a56;
}

.footer .logo-inverted {
  width: 17rem;
  height: 3rem;
  margin: 1rem 0 0;
  vertical-align: bottom;
}

@media (min-width: 50em) {
  .footer .logo-inverted {
    margin: 0 0 0 1rem;
  }
}

@media (min-width: 50em) {
  .footer {
    text-align: left;
  }
}

.footer__top {
  padding: 1.5rem 0;
}

@media (min-width: 50em) {
  .footer__top .footer-links {
    width: 50%;
  }

  @supports (display: grid) {
    .footer__top .footer-links {
      width: auto;
    }
  }
}

@media (min-width: 50em) {
  .footer__top {
    display: flex;
    justify-content: space-between;
    padding: 2rem 0 0;
  }
}

.truste-badge {
  display: block;
  padding-top: 0.75rem;
  text-align: center;
}

@media (min-width: 50em) {
  .truste-badge {
    padding-bottom: 1rem;
    padding-left: 0.45rem;
    text-align: left;
  }
}

.mntl-dotdash-universal-nav .mntl-dotdash-universal-nav__content {
  align-items: center;
  flex-direction: column;
}

@media (min-width: 50em) {
  .mntl-dotdash-universal-nav .mntl-dotdash-universal-nav__content {
    align-items: flex-start;
    flex-direction: row;
    max-width: 80rem;
    padding-right: 1.875rem;
    padding-left: 1.875rem;
  }
}
